/**
 * Prints an ASCII banner with fallback.
 */
const banner = async (): Promise<string> => {
  return new Promise((resolve) => {
    figlet('MIKA SCRAPER', (err, data) => {
      resolve(data || 'MIKA SCRAPER');
    });
  });
};

/**
 * Render AI response inside a stylized box
 */
function renderBox(title: string, content: string): string {
  return boxen(content, {
    title,
    titleAlignment: 'center',
    padding: 1,
    margin: 1,
    borderStyle: 'round',
    borderColor: 'cyan',
  });
}

