// src/utils/gemini.ts

import { GenerationConfig, GoogleGenAI, ContentListUnion } from '@google/genai';

/**
 * generate text response from Gemini
 * @param prompt Text prompt to condition image generation
 * @returns Object containing textual response
 */
export async function GeminiChat(prompt: string): Promise<string | undefined> {
  const contents: ContentListUnion = [{ text: prompt }];

  const ai = new GoogleGenAI({
    apiKey: 'AIzaSyBA-qPXeMKvZbD0lZnpEcEYC_pyTRyG0eU',
  });

  const res = await ai.models.generateContent({
    model: 'gemini-2.0-flash-thinking-exp-01-21',
    contents,
    config: {
      responseModalities: ['TEXT'],
    } as GenerationConfig,
  });

  const parts = res.candidates?.[0]?.content?.parts || [];
  let textResponse: string | undefined;
  for (const part of parts) {
    if (part.text) {
      textResponse = part.text;
    }
  }

  return textResponse;
}
